.MainReg{ 
    background: #FFF; 
  	box-sizing: border-box;
  	position: absolute;
  	height: 90vh;
  	width: 100%;
}
.TextBlockReg{
	color: #000;
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 5%;
}
.ImgPosition{
	position: absolute;
	top: 60%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%);
}
.ImgCodeRegistration{
	display: block;
  	width: 80%;
  	margin-left: auto;
	margin-right: auto; 
	max-width: 350pt;
}
.InputReg{
	margin-top: 5%;
	margin-bottom: 5%;
}
.ButtonReg{
	background-color: #6F389F;
	border-color: #6F389F;
	color: #FFF; 
	font-family: 'Century Gothic';
	font-size: 18pt;
}
.ButtonBlockReg{
	position: absolute;
	top: 85%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%);
	width: 70%;
}
.FontButtonReg{
	font-family: 'Century Gothic';
	font-size: 11pt;
	font-weight: bold;
	color: #FFF; 
	width: 100pt;
	height: 13pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}
.TextNotify{
	color: #000;
	font-family: 'Calibri Light';
	font-size: 14pt;
	text-align: center;
	margin-top: 10pt;
	margin-bottom: 20pt;
}
.ButtonBlockRegHidden{
	margin-right: 10vw;
	margin-left: 10vw;
	display: none;
}