.MainMenu{ 
    background: #FFF; 
  	box-sizing: border-box;
  	position: absolute;
  	height: 90vh;
  	width: 100%;
}
.TextBlockMain{
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 5%;
	color: #000;
}
.ImgPositionHome{
	position: absolute;
	top: 35%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%);
}
.ImgHome{
	display: block;
  	width: 80%;
  	margin-left: auto;
	margin-right: auto; 
	max-width: 400px;
}
.TextBlockMainSecond{
	color: #000;
	text-align: center;
	font-size: 18pt;
	font-family: 'Calibri Light';
	margin-left: 30pt;
    margin-right: 30pt; 
}
.ButtonBlcokMain{
	margin-top: 5%;
	background-color: #6F389F;
	border-color: #6F389F;
	color: #FFF; 
	font-family: 'Century Gothic';
	font-size: 18pt;
}
.ButtonBlocksMain{
	width: 70%;
	position: absolute;
	top: 80%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%);
}
.FontButtonMain{
	font-family: 'Century Gothic';
	font-size: 11pt;
	font-weight: bold;
	color: #FFF; 
	width: 150pt;
	height: 13pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}
.MyRecordBlock{
	width: 100%;
	height: 50px;
	border-color: #000;
	border-width: 2px;
	text-align: center;
	vertical-align:text-top;
	padding: 0px 0px 0px 0px;
}
.TextMyRecord{
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
	font-size: 18pt;
	font-family: 'Calibri Light';
	display:inline-block;
}
.CloseButton{
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 5px;
	display:inline-block;
	background-image: url(../img/closeButton.png);
	background-repeat: no-repeat;
	background-size: contain;
	border-radius: 70px;
	color: white;
	width: 35px;
	height: 35px;
	background-position: center;
}
.DeleteButton{
	width: 70px;
	margin-left: 2%;
	margin-right: 2%;
	font-family: 'Calibri Light';
	font-size: 16pt;
	color: white;
	background-color : #4672C4;
}
.TextAlertConfirm{
	font-family: 'Calibri Light';
	font-size: 16pt;
	text-align: center;
}
