.MainRecord{ 
    background: #FFF; 
  	box-sizing: border-box;
  	position: absolute;
  	height: 90vh;
  	width: 100%;
}
.TextBlockRecord{
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 10pt;
	margin-bottom: 50pt;
	color: #000;
}
.RecordBlock{
	display: block;
	text-align: center;
}
.ButtonMothsDay{
	margin-left: 2%;
	margin-right: 2%;
	background-color: #FFF;
	border-color: #C0C0C0;
	color: #4672C4; 
	font-family: 'Calibri Light';
	margin-bottom: 10pt;
}/*
.ButtonMothsDay:focus{
	background-color: #D3D3D3;
}*/

.FontClass{
	font-family: 'Calibri Light';
	font-size: 12pt;
	font-weight: bold;
}
.RecordBlockDataTime{
	display: block;
	text-align: center;
}
.ButtonDataTime{
	position: relative;
	margin-left: 4%;
	margin-right: 4%;
	border-color: #FFF;
	width: 55pt;
	height: 55pt;
	border-radius: 40px;
	background-image: url(../img/VacantDay.png);
	​​​​​​​background-repeat: no-repeat;
	background-size: contain;
}
.ButtonDataTime:focus{
	opacity: 0.6;
}
.TimeRecord{
	width: 40pt;
	height: 9pt;
	display: block;
	border-color: #434342;
	font-family: 'Calibri Light';
	font-size: 10pt;
	color: #FFF; 
	text-align: center;
	margin-bottom: 10pt;

}
.CountRecord{
	color: #4672C4;
	display: block;
	position: absolute;
	border-color: #000;
	text-align: center;
	font-size: 8pt;
	right: 5%;
	left: 5%;
	bottom: 0.1%;
}

.ButtonBlcokRecord{
	margin-top: 20pt;
	background-color: #6F389F;
	border-color: #6F389F;
	text-align: center;
}
.FontButtonRecord{
	font-family: 'Century Gothic';
	font-size: 11pt;
	font-weight: bold;
	color: #FFF; 
	width: 200pt;
	height: 13pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}

.ButtonBlocksRecord{
	margin-right: 10vw;
	margin-left: 10vw;
	margin-bottom: 20pt;
}
.Dasha {
  display: block;
  width: 50%;
  max-width: 240px;
  margin: 20px auto;
}
.ButtonsCountWashingMachines{
	margin-bottom: 2%;
}
.ButtonWashMachine{
	border-color: #4672C4;
	border-width: 2px;
	margin-left: 1%;
	margin-right: 1%;
}
.TextComplete{
	color: #000;
	font-family: 'Calibri Light';
	font-size: 14pt;
	text-align: center;
}