.Main{ 
    background: #FFF; 
  	box-sizing: border-box;
  	position: absolute;
  	width: 100%;
  	height: 90vh;
}
.TextBlock{
	color: #000;
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 5%;
}
.MainBlock{
}
.ImgWelcomeBlock{
	position: absolute;
	top: 50%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%)
}
.ImgWelcome{
	margin-left: auto;
	margin-right: auto;
	display: block;
  	width: 70%;
	max-width: 200px;
}
.TextFirstBlock{
	color: #000;
	text-align: center;
	font-size: 14pt;
	font-family: 'Calibri Light';
}
.ButtonPosition{
	position: absolute;
	top: 85%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%);
	width: 70%;
}
.ButtonBlock{
	margin-top: 20pt;
	background-color: #6F389F;
	border-color: #6F389F;
	color: #FFF; 
	font-family: 'Century Gothic';
	font-size: 18pt;
}
.FontButtonStart{
	font-family: 'Century Gothic';
	font-size: 11pt;
	font-weight: bold;
	color: #FFF; 
	width: 100pt;
	height: 13pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}

