.MainCode{ 
    background: #FFF; 
  	box-sizing: border-box;
  	position: absolute;
  	height: 90vh;
  	width: 100%;
}
.TextBlockCode{
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 10pt;
	color: #000;
	margin-bottom: 20pt;
}
.ImgCodeView{
	display: block;
  	width: 90%;
  	margin-left: auto;
    margin-right: auto; 
    margin-bottom: 40pt;
}
.QRCode{
	position: absolute;
	top: 50%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%);
}
.ImgCodeViewScreen{
	display: block;
  	width: 100%;
	margin: 0px 0px 0px 0px; 
}
.ButtonBlcokCode{
	margin-top: 20pt;
	margin-bottom: 20pt;
	background-color: #6F389F;
	border-color: #6F389F;
	color: #FFF; 
	font-family: 'Century Gothic';
	font-size: 18pt;
}
.ButtonBlockCodeAlign{
	position: absolute;
	top: 85%;
	left: 50%;
	margin-right: -50%;
	width: 70%;
	transform: translate(-50%, -50%)
}
.FontButtonCode{
	font-family: 'Century Gothic';
	font-size: 11pt;
	font-weight: bold;
	color: #FFF; 
	width: 100pt;
	height: 13pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}
