.MainRecordDay{
    background: #FFF; 
    box-sizing: border-box;
    position: absolute;
    height: 90vh;
    width: 100%;
}
.TextChooseDay{
    font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 5%;
	color: #000;
}
.Calendar{
    width: 100%;
    position: absolute;
	top: 50%;
	left: 50%;
	margin-right: -50%;
    transform: translate(-50%, -50%);
    border-color: #000;
    border: 3px;
}
.RowButton{
    width: 100%;
    padding: 0px 0px 0px 0px;
}
.RowButton > Div{
    background: #eee;
    float: left;
    width: 14.25%;
    box-sizing: border-box;
}
.ButtonDay{
    position: relative;
    height: 10vh;
    padding-left: 1%;
    padding-right: 1%;
}
.PositionButton{
    width: 100%;
    height: 100%;
    border-radius: -2%;
    position: absolute;
    top: 50%;
	left: 50%;
	margin-right: -50%;
    transform: translate(-50%, -50%);
}